  loadDossierActions: function loadDossierActions(data) {
    var vm = this;
    var submissionNote = '';

    try {
      submissionNote = vm.thongTinChiTietHoSo['submissionNote'] ? JSON.parse(vm.thongTinChiTietHoSo['submissionNote']) : '';
    } catch (error) {}

    if (submissionNote && submissionNote.hasOwnProperty('dossierImport') && submissionNote.dossierImport) {
      vm.sequencyDossierImport = true;
      vm.dossierImportActions = submissionNote['data'];
    } else {
      vm.sequencyDossierImport = false;

      if (vm.thongTinChiTietHoSo.dossierId) {
        var dataParams = {
          dossierId: vm.thongTinChiTietHoSo.dossierId,
          stepType: data
        };
        vm.$store.dispatch('loadDossierActions', dataParams).then(function (resultActions) {
          if (resultActions.data && resultActions.data.length !== 0) {
            var resultTemp = resultActions.data;

            for (var i = 0; i < resultTemp.length; i++) {
              if (resultTemp[i].hasOwnProperty('actions') && resultTemp[i]['actions'] !== null && resultTemp[i]['actions'] !== undefined) {
                if (!Array.isArray(resultTemp[i]['actions'])) {
                  var arrActionsTemp = [];
                  arrActionsTemp.push(resultTemp[i]['actions']);
                  resultTemp[i]['actions'] = arrActionsTemp;
                }
              }
            }

            vm.dossierActions = resultTemp;
          } else {
            if (vm.thongTinChiTietHoSo['submissionNote']) {
              try {
                JSON.parse(vm.thongTinChiTietHoSo['submissionNote']);
                var _resultTemp = JSON.parse(vm.thongTinChiTietHoSo['submissionNote']).data;

                for (var i = 0; i < _resultTemp.length; i++) {
                  if (_resultTemp[i].hasOwnProperty('actions') && _resultTemp[i]['actions'] !== null && _resultTemp[i]['actions'] !== undefined) {
                    if (!Array.isArray(_resultTemp[i]['actions'])) {
                      var _arrActionsTemp = [];

                      _arrActionsTemp.push(_resultTemp[i]['actions']);

                      _resultTemp[i]['actions'] = _arrActionsTemp;
                    }
                  }
                }

                vm.dossierActions = _resultTemp;
              } catch (e) {}
            }
          }
        }).catch(function () {
          if (vm.thongTinChiTietHoSo['submissionNote']) {
            try {
              JSON.parse(vm.thongTinChiTietHoSo['submissionNote']);
              var resultTemp = JSON.parse(vm.thongTinChiTietHoSo['submissionNote']).data;

              for (var i = 0; i < resultTemp.length; i++) {
                if (resultTemp[i].hasOwnProperty('actions') && resultTemp[i]['actions'] !== null && resultTemp[i]['actions'] !== undefined) {
                  if (!Array.isArray(resultTemp[i]['actions'])) {
                    var arrActionsTemp = [];
                    arrActionsTemp.push(resultTemp[i]['actions']);
                    resultTemp[i]['actions'] = arrActionsTemp;
                  }
                }
              }

              vm.dossierActions = resultTemp;
            } catch (e) {}
          }
        });
      }
    }
  },
