    processListCongVan: function processListCongVan(currentQuery) {
      var vm = this;

      var getAllUrlParams = function getAllUrlParams(arr) {
        var obj = {};

        for (var i = 0; i < arr.length; i++) {
          var a = arr[i].split('=');
          var paramName = a[0];
          var paramValue = typeof a[1] === 'undefined' ? true : a[1];
          paramName = paramName.toLowerCase();
          if (typeof paramValue === 'string') paramValue = paramValue.toLowerCase();

          if (paramName.match(/\[(\d+)?\]$/)) {
            var key = paramName.replace(/\[(\d+)?\]/, '');
            if (!obj[key]) obj[key] = [];

            if (paramName.match(/\[\d+\]$/)) {
              var index = /\[(\d+)\]/.exec(paramName)[1];
              obj[key][index] = paramValue;
            } else {
              obj[key].push(paramValue);
            }
          } else {
            if (!obj[paramName]) {
              obj[paramName] = paramValue;
            } else if (obj[paramName] && typeof obj[paramName] === 'string') {
              obj[paramName] = [obj[paramName]];
              obj[paramName].push(paramValue);
            } else {
              obj[paramName].push(paramValue);
            }
          }
        }

        return obj;
      };

      var filter = {
        paramSearch: vm.trangThaiHoSoList[vm.index]['queryParams'] ? getAllUrlParams(vm.trangThaiHoSoList[vm.index]['queryParams'].split('?')[1].split('&')) : {}
      };
      var agencyDonViNhan = vm.trangThaiHoSoList[vm.index]['tableConfig'].hasOwnProperty('searchCongVanTheoDonViNhan') ? vm.trangThaiHoSoList[vm.index]['tableConfig']['searchCongVanTheoDonViNhan'] : '';
      filter.paramSearch = Object.assign(filter.paramSearch, {
        searchCongVanTheoDonViNhan: agencyDonViNhan
      });
      vm.$store.dispatch('getListCongVan', filter).then(function (result) {
        if (result.length > 0) {
          vm.listCongVan = result.map(function (cv) {
            cv['displayName'] = cv['documentNo'] + ' - ' + cv['documentDate'].split(' ')[0] + ' - ' + cv['govAgencyName'];
            return cv;
          });
        }

        if (currentQuery.hasOwnProperty('groupDossierId') && String(currentQuery.groupDossierId) !== '') {
          for (var key in vm.listCongVan) {
            if (String(vm.listCongVan[key]['dossierId']) === String(currentQuery.groupDossierId)) {
              vm.congvanSelected = vm.listCongVan[key];
            }
          }
        } else {
          vm.congvanSelected = null;
        }
      }).catch(function () {});
    },
