    doPrint03: function doPrint03(dossierItem, item, index, isGroup) {
      var vm = this;
      vm.dialogPDFLoading = true;
      vm.dialogPDF = true;
      var filter = {
        dossierId: dossierItem.dossierId,
        document: item ? item.document : ''
      };
      var counter = 0;

      var callServer = function callServer() {
        setTimeout(function () {
          vm.$store.dispatch('doPrint03', filter).then(function (result) {
            if (result === 'pending' && counter <= 10) {
              counter += 1;
              callServer();
            } else {
              if (counter > 10) {
                vm.dialogPDFLoading = false;
              } else {
                vm.dialogPDFLoading = false;
                vm.dialogPDF = true;
                setTimeout(function () {
                  document.getElementById('dialogPDFPreview').src = result;
                }, 100);
              }
            }
          });
        }, 1000);
      };

      callServer();
    },
    doGuiding: function doGuiding(type) {
      var vm = this;

      if (vm.$refs.formGuide.validate()) {
        vm.loadingAction = true;
        var partNoRequired = [];

        for (var key in vm.tphsGuide) {
          if (!vm.tphsGuide[key]['fileMark']) {
            partNoRequired.push(vm.tphsGuide[key]['partNo']);
          }
        }

