    doCreateDossier: function doCreateDossier() {
      var vm = this;
      var data = {
        serviceCode: vm.serviceCode,
        govAgencyCode: vm.govAgencyCode,
        templateNo: vm.templateNo,
        originality: vm.itemAction['form'] === 'NEW_GROUP' || vm.itemAction['form'] === 'NEW_GROUP_CV' || vm.itemAction['form'] === 'NEW_GROUP_CV_DI' ? 9 : vm.getOriginality() // add new template

      };
      var filter = {
        dossierTemplateNo: vm.templateNo
      };
      vm.$store.dispatch('loadDossierFormTemplates', filter).then(function (result1) {
        var mauCongVan = result1['newFormScript'] && result1['newFormScript'].startsWith('MAU_CV_') ? result1['newFormScript'] : false;

        if (result1['newFormScript'] && !mauCongVan) {
          vm.$router.push({
            path: '/danh-sach-ho-so/0/ho-so/0/' + vm.itemAction.form,
            query: vm.$router.history.current.query
          });
        } else {
          vm.$store.commit('setDataCreateDossier', data);
          vm.loadingAction = true;
          var query_redirect = vm.$router.history.current.query;

          if (vm.itemAction['form'] === 'NEW_GROUP_CV' || vm.itemAction['form'] === 'NEW_GROUP_CV_DI') {
            query_redirect = Object.assign(query_redirect, {
              formActionGroup: JSON.stringify(vm.itemAction)
            });
          }

          vm.$store.dispatch('postDossier', data).then(function (result) {
            vm.loadingAction = false;
            vm.indexAction = -1;
            vm.$router.push({
              path: '/danh-sach-ho-so/' + vm.index + '/ho-so/' + result.dossierId + '/' + vm.itemAction.form,
              query: query_redirect
            });
          }).catch(function (reject) {
            vm.loadingAction = false;
          });
        }
      }).catch(function () {}); //
    },
