    putGroupDossier: function putGroupDossier(draf) {
      var vm = this;
      var currentParams = vm.$router.history.current.params;
      var currentQuery = vm.$router.history.current.query;

      if (vm.formCode === 'NEW_GROUP') {
        var thongtinnguoinophoso = this.$refs.thongtinnguoinophoso ? this.$refs.thongtinnguoinophoso.thongTinNguoiNopHoSo : {};
