                  return item.actionCode === 1100 || item.actionCode === '1100';
                });
                vm.$store.dispatch('processPullBtnDetail', {
                  dossierId: result.dossierId,
                  actionId: actionDetail[0] ? actionDetail[0].processActionId : ''
