    currentcyToString: function currentcyToString(so) {
      var i;
      var j;
      var kq = "";
      var l;
      var dk;
      var tmp = "";
      var check = false;
      var a = new Array(32);

      while (so.length > 0 && so.charAt(0) == "0") {
        so = so.substring(1, so.length);
      }

      l = so.length;

      if (l > 28) {
        return "So khong hop le";
      }

      for (var i = 1; i <= l; i++) {
        so = so.replace(',', '');
      }

      l = so.length;

      for (var i = 1; i <= l; i++) {
        a[i] = parseInt(so.charAt(i - 1));
      } //Bat dau doc tu trai sang phai


      for (var i = 1; i <= l; i++) {
        if ((l - i) % 3 == 2 && a[i] == 0 && l - i >= 2) {
          if (a[i + 1] != 0 || a[i + 2] != 0) {
            kq = kq + "không ";
          }
        }

        if (a[i] == 2) {
          kq = kq + "hai ";
        }

        if (a[i] == 3) {
          kq = kq + "ba ";
        }

        if (a[i] == 6) {
          kq = kq + "sáu ";
        }

        if (a[i] == 7) {
          kq = kq + "bảy ";
        }

        if (a[i] == 8) {
          kq = kq + "tám ";
        }

        if (a[i] == 9) {
          kq = kq + "chín ";
        } //Xu ly cach doc so 4


        if (a[i] == 4) {
          if (i > 1 && (l - i) % 3 == 0) {
            if (a[i - 1] > 1) {
              kq = kq + "tư ";
            } else {
              kq = kq + "bốn ";
            }
          } else {
            kq = kq + "bốn ";
          }
        } //Xu ly cach doc so 5


