            } else {
              kq = kq + "năm ";
            }
          } else {
            kq = kq + "năm ";
          }
        } //Xu ly cach doc so 1


        if (a[i] == 1) {
          //doc la muoi neu no la hang chuc
          if ((l - i) % 3 == 1) {
            kq = kq + "mười "; //doc la mot neu la hang don vi	//va hang chuc >1
          } else {
            if ((l - i) % 3 == 0 && i > 1) {
              if (a[i - 1] > 1) {
