                kq = kq + "một ";
              }
            } else {
              kq = kq + "một ";
            }
          }
        }

        if ((l - i) % 3 == 1 && a[i] != 0 && a[i] != 1) {
          kq = kq + "mươi ";
        }

        if ((l - i) % 3 == 1 && a[i] == 0 && a[i + 1] != 0) {
          kq = kq + "linh ";
        }

        if ((l - i) % 3 == 2 && (a[i + 1] != 0 || a[i + 2] != 0)) {
          kq = kq + "trăm ";
        }

        if (i + 2 <= l) {
          if (a[i] != 0 && (l - i) % 3 == 2) {
            if (a[i + 1] == 0 && a[i + 2] == 0) {
              kq = kq + "trăm ";
            }
          }
        }

        if (l - i == 3) {
          kq = kq + "nghìn ";
        }

        if (l - i == 6) {
          kq = kq + "triệu ";
        }

        if (l - i == 9) {
          kq = kq + "tỷ ";
        }

        if (l - i == 12) {
          check = true;

          for (j = i + 1; i < l; i++) {
            if (a[i + 1] != 0) {
              check = false;
            }
          }

          if (check == false) {
            kq = kq + "nghìn ";
          } else {
            kq = kq + "nghìn tỷ ";
          }
        }

        if (l - i == 15) {
          kq = kq + "triệu tỷ ";
