        if (l - i == 18) {
          kq = kq + "tỷ tỷ ";
        }

        if (l - i == 21) {
          kq = kq + "nghìn tỷ tỷ ";
        }

        if (l - i == 24) {
          kq = kq + "triệu tỷ tỷ ";
        }

        if (l - i == 27) {
          kq = kq + "tỷ tỷ tỷ ";
        }

        if (l - i == 30) {
          kq = kq + "nghìn tỷ tỷ ";
        } //Xu ly bo 3 so khong


        if ((l - i) % 3 == 2 && a[i] == 0 && a[i + 1] == 0 && a[i + 2] == 0) {
          i = i + 2;
        } //Xu ly tat ca so khong con lai


        if ((l - i) % 3 == 0) {
          dk = 1;

          for (j = i + 1; j <= l; j++) {
            if (a[j] != 0) {
              dk = 0;
            }
          }
        }

        if (dk == 1) {
          break;
        }
      } //Viet hoa chu cai dau tien


      if (kq == "") kq = "không ";

      while (kq.charAt(kq.length) == ",") {
        kq = kq.substring(0, kq.length - 1);
      }

      kq = kq.charAt(0).toUpperCase() + kq.substring(1, kq.length);

      if (kq != "So khong hop le") {
        kq = kq + "đồng chẵn";
      } else {
        kq = kq;
      }

      return kq;
    }
